<template>
  <div class="content">
    <section class="formContent">
      <form action="#">
        <div class="formLine" @click="openPickerTime">
          <p>开始时间</p>
          <p>{{currentDateTime}}</p>
        </div>
        <div class="formLine" @click="openPickerTime">
          <p>结束时间</p>
          <p>{{currentDateTime}}</p>
        </div>
        <div class="formLine" @click="openPickerRegion">
          <p>所在区域</p>
          <p>{{currentRegion}}</p>
        </div>
        <div class="formLine">
          <p>员工工号</p>
          <input type="text" name id placeholder="请输入员工工号" />
        </div>
      </form>
      <div class="submit" @click="$router.push({path:'/cenusDetail'})">确认查询</div>
    </section>
    <section class="pickerDataTime">
      <van-overlay :show="show" @click="show = false" />
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        v-show="openPickerActive"
        @cancel="openPickerActive = false ; show = false"
        @confirm="confirmTime"
      />
      <van-picker
        v-show="openRegionActive"
        show-toolbar
        :columns="columns"
        @cancel="openRegionActive = false ; show = false"
        @confirm="onConfirm"
      />
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      openPickerActive: false,
      openRegionActive: false,
      minDate: new Date(),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      currentDateTime: this.util.getTime(new Date()),
      currentRegion: "",
      show: false,
      columns: [
        "一楼大厅区",
        "vip大厅区",
        "二楼大厅区",
        "二楼vip区",
        "一楼大厅区"
      ]
    };
  },
  mounted() {
    this.currentRegion = this.columns[0];
  },
  methods: {
    openPickerTime() {
      this.openPickerActive = !this.openPickerActive;
      this.show = !this.show;
    },
    openPickerRegion() {
      this.openRegionActive = !this.openRegionActive;
      this.show = !this.show;
    },
    confirmTime(value) {
      this.currentDateTime = this.util.getTime(value);
      this.openPickerActive = !this.openPickerActive;
      this.show = !this.show;
    },
    onConfirm(value, index) {
      this.currentRegion = value;
      this.openRegionActive = !this.openRegionActive;
      this.show = !this.show;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  .formContent {
    background-color: #fff;
    box-shadow: 0px 0.3rem 0.8rem $Tline;
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin: 0.5rem;
    .formLine {
      @include fr(space-between);
      padding: 0.5rem;
      margin: 0.5rem;
      border-bottom: 0.05rem solid $F5;
    }
    input {
      text-align: right;
    }
  }
  .pickerDataTime {
    position: fixed;
    bottom: 0;
    z-index: 2;
    @include wh(100%, auto);
  }
}
/deep/ .van-datetime-picker,.van-picker {
  z-index: 2;
}
</style>